.ap-container {
    padding: 10px;
    max-width: 100%;
}

.ap-container p{
    line-height: 0;
    font-size: 13px;
    text-align: left;
    width: 70%;
    float: right;
    color:rgba(255, 255, 255, 0.5);
}


.ap-avatar-holder {
    z-index: 1;
    position: absolute;
    border-radius: 50%;
    margin-top: -0px;
    margin-left: 30px;
    width: 100px;
    height: 100px;
    background-image: url('https://thumbs.dreamstime.com/b/head-shot-portrait-social-networks-dating-application-profile-picture-attractive-young-happy-indian-arabic-mixed-race-woman-203598050.jpg');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    filter: drop-shadow(0 0 10px rgba(0, 0, 0, 0.2));
}

.ap-container audio {
    height: 50px;
    border-radius: 0;
}

.ap-container audio::-webkit-media-controls-play-button {
    margin-left: 30px;
}

.ap-container audio::-webkit-media-controls-mute-button,
.ap-container audio::-webkit-media-controls-play-button {
    filter: invert(0.7);
}
