.bvp-video-container {
  padding: 20px 0;
  width: 100%;
}

.bvp-video-container video {
  width: 100%;
  height: auto;
  outline: none;
}

.bvp-video-container video::-webkit-media-controls-play-button,
.bvp-video-container video::-webkit-media-controls-mute-button,
.bvp-video-container video::-webkit-media-controls-seek-back-button,
.bvp-video-container video::-webkit-media-controls-fullscreen-button,
.bvp-video-container video::-webkit-media-controls-seek-back-button,
.bvp-video-container video::-webkit-media-controls-seek-forward-button {
  filter: invert(1);
} 
