
.container {
  display: grid;
  height: 100%; 
  max-height: 100vh;
  grid-template-columns: 2.1fr 0.6fr; 
  grid-template-rows: 0.08fr 2.4fr; 
  gap: 0px 0px; 
  grid-template-areas: 
    "nav-topleft nav-topright"
    "main sidebar"; 
}


.main { 
  grid-area: main; 
  max-height: 100vh;
  background-color: #ececec;
  box-shadow: inset 0px 0px 20px rgba(0,0,0,0.7);
  overflow-y: scroll;
}

.main::-webkit-scrollbar {
  width: 12px;               /* width of the entire scrollbar */
}

.main::-webkit-scrollbar-track {
  background: rgb(61, 61, 61);        /* color of the tracking area */
}

.main::-webkit-scrollbar-thumb {
  background-color: rgb(139, 139, 139);    /* color of the scroll thumb */
  border-radius: 20px;       /* roundness of the scroll thumb */
}

.sidebar { 
  grid-area: sidebar; 
  background-color: #1b1b1b;
  color: #fff;
}

.nav-topright { 
  height: 100%;
  max-height: 100%;
  grid-area: nav-topright; 
  background-color: #04bf2d;
}

.nav-responsive-holder {
  display: flex;
  align-items: center;
  align-content: center;
  height: 100%;
}

#nav-responsive-btn-not-selected {
  opacity: 0.7;
}

#nav-responsive-btn-selected {
  background-color: #ffffff3a;
  opacity: 1;
  border-radius: 50%;
  transform: scale(0.9);
  filter: drop-shadow(0 0 10px white);
}

.nav-responsive-holder button {
  background-color: transparent;
  border-width: 0;
  padding: 10px;
  opacity: 0.8;
}

.nav-responsive-holder button:hover {
  background-color: rgba(255, 255, 255, 0.11);
  border-radius: 50%;
  cursor: pointer;
  opacity: 1;
}

.nav-responsive-holder button:active {
  background-color: rgba(255, 255, 255, 0.3);
}

.nav-responsive-holder img {
  height: 20px;
  filter: invert(1);
  opacity: inherit;
}

.nav-topleft {
  height: 100%; 
  grid-area: nav-topleft; 
  background-color: #04bf7b;
}

.nav-topright {
  height: 100%; 
  grid-area: nav-topright; 
  background-color: #04bf7b;
}

.page-holder {
  margin: 0 auto;
  min-height: 100vh;
  background-color: transparent;
  filter: drop-shadow(0 0 0px rgba(126, 126, 126, 0.466));
  transition: 0.3s;
}

.page-holder h1 {
  color: #04bf7a;
}
.page-holder h2 {
  color: #6bc7a5;
}

.page-holder p, 
.page-holder div {
  color: #7a7a7a;
}

.page-holder *:focus {
  outline: 2px solid #04bf7a;
}

.page-holder-enabled div, 
.page-holder-enabled h1, 
.page-holder-enabled h2, 
.page-holder-enabled p, 
.page-holder-enabled br {
  border: 1px dashed rgba(128, 128, 128, 0.438);
  min-height: 20px;
}


.page-holder br { 
  visibility: visible;
  width: 100%;
  height: 10px;
  background-color: #04bf7a;
  padding: 10px;
  border: 1px solid rgba(128, 128, 128, 0.8)
}

.page-holder-enabled h1 {
  min-height: 50px;
}

.page-holder-enabled h2 {
  min-height: 35px;
}

.page-holder-disabled {
  pointer-events: none;
}

.tb-holder {
  height: 50%;
  max-height: 50vh;
  overflow-y: scroll;
  background-color: #292929;
}

.tb-holder::-webkit-scrollbar {
  display: none; /* for Chrome, Safari, and Opera */
}

.tb-item-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: flex-start;
  align-content: flex-start;
}

.disabled-menu-item {
  opacity: 0.5;
  pointer-events: none;
}

.mc-item-holder {
  text-align: center;
  background-color: #ffffff0e;
  color: rgb(235, 235, 235);
  width: 30%;
  margin: 1%;
  padding: 2%;
  border: 3px solid rgba(124, 124, 124, 0.678);
  border-radius: 5px;
  transition: 0.3s;
}

.mc-item-holder img{
  margin: 0 auto;
  height: 50px;
  filter: invert(1);
  object-fit: fill;
  transition: inherit;
  opacity: 0.6;
}

.mc-item-holder p{
  font-weight: 700;
}

.mc-item-holder:hover {
  cursor: grab;
  border: 3px solid #04bf7b;
}

.mc-item-holder:active {
  cursor: grabbing;
}
.mc-item-holder:focus {
  cursor: grabbing;
}

.mc-item-holder:hover img{
  transform: scale(1.1);
  opacity: 1;
}

.dropdown-btn {
  width: 100%;
  padding: 10px;
  background-color: #353535;
  color: #cccccc;
  border-width: 0;
  text-align: left;
}

.dropdown-btn:hover {
  cursor: pointer;
  background-color: #3f3f3f;
  color: #eceaea;
}

.settings-panel-container {
  padding: 10px;
}

.settings-panel-container p {
  line-height: 0px;
  color: rgba(255, 255, 255, 0.692);
}
.settings-panel-container hr {
  line-height: 0;
  border: 1px solid rgba(255, 255, 255, 0.192);
}

.sp-selected {
  padding: 5px 10px;
  background-color: #04bf7a;
  border-radius: 50px;
  text-align: center;
}

[contenteditable][placeholder]:empty:before {
  content: attr(placeholder);
  position: absolute;
  color: rgba(0, 0, 0, 0.308);
  background-color: transparent;
}

.uc-holder {
  width: 100%;
}

.form-group-files {
  margin: 0 auto;
  max-width: 400px;
  border: 3px dashed green;
  background-color: white;
  color: #8b8b8b;
  padding: 10px;
  filter: drop-shadow(0 0 10px rgba(0, 0, 0, 0.151));
  border-radius: 10px;
}

.input-form-control {
  outline-width: 0;
  padding: 8px 10px;
  border: 0px solid rgb(165, 165, 165);
  background-color: #f3f3f3;
  border-radius: 10px;
  width: 90%;
}

#dropzone {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #f3f3f3;
  height: 150px;
  text-align: center;
  vertical-align: middle;
  border-radius: 10px;
  border: 2px dashed #04bf7a;
}

.btn-open-file-selector {
  justify-content: flex-end;
  border: 0px solid #04bf7a;
  padding: 10px;
  border-radius: 30px;
  background-color: #04bf7a;
  color: #ececec;
  transition: 0.3s;
}

.btn-open-file-selector:hover {
  cursor: pointer;
  background-color: #494949;
}

#exlude-border {
  border-width: 0;
}

.dashed-line-break {
  display: flex;
  flex-direction: row;
}
.dashed-line-break:before, .dashed-line-break:after{
  content: "";
  flex: 1 1;
  height: 5px;
  border-bottom: 2px solid;
  color: #bebebe;
  margin: auto;
}
.dashed-line-break:before {
  margin-right: 10px
}
.dashed-line-break:after {
  margin-left: 10px
}

#empty-container {
  visibility: hidden;
  margin: 0;
  padding: 0;
}

#empty-container div {
  visibility: visible;
}

#empty-container:only-child {
  display: unset;
}

.grids-full-holder {
  border: 2px solid rgb(223, 223, 223);
  background-color: #f5f5f5;
  filter: drop-shadow(0 0 10px rgba(0, 0, 0, 0.1));
  border-radius: 5px;
  display: flex;
  padding: 10px 10px;
}

.grids-half-holder {
  border: 2px solid rgb(223, 223, 223);
  background-color: #f5f5f5;
  filter: drop-shadow(0 0 10px rgba(0, 0, 0, 0.1));
  border-radius: 5px;
  display: flex;
  padding: 10px;
}

.grids-quarter-holder {
  border: 2px solid rgb(223, 223, 223);
  background-color: #f5f5f5;
  filter: drop-shadow(0 0 10px rgba(0, 0, 0, 0.1));
  border-radius: 5px;
  display: flex;
  padding: 10px;
}

.grid-full-container {
  width: 100%;
  padding: 20px 10px;
}

.grid-half-container {
  width: 50%;
  padding: 20px 10px;
}

.grid-quarter-container {
  width: 25%;
  padding: 20px 10px;
}

.grid-item-template {
  min-width: 25%;
  max-width: 100%;
  padding: 20px 10px;
}

.grids-itemwrapper-holder {
  border: 2px solid rgb(223, 223, 223);
  background-color: #f5f5f5;
  filter: drop-shadow(0 0 10px rgba(0, 0, 0, 0.1));
  border-radius: 5px;
  padding: 10px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: stretch;
}

.grids-itemwrapper-holder>div {
  flex: 1;
  flex-grow: 1;
}

.qh-holder, .small-card-holder {
  max-width: 800px;
  margin: 0 auto;
  background-color: white;
  filter: drop-shadow(0 0 10px rgba(0, 0, 0, 0.123));
  padding: 10px 20px;
  border-radius: 10px;
}

.qh-holder textarea {
  background-color: #f5f5f5;
  filter: drop-shadow(0 0 5px rgba(0, 0, 0, 0.1));
  border-width: 0;
  resize: none;
  width: 99%;
  height: 100px;
  border-radius: 10px;
  padding: 10px;
  margin: 0 10px 0 0;
}

.qh-holder button, 
.small-card-holder button {
  background-color: #04bf7a;
  border-width: 0;
  padding: 10px;
  color: white;
  border-radius: 10px;
  margin: 10px;
}
.qh-holder button:hover, 
.small-card-holder button:hover {
  background-color: #494949;
  cursor: pointer;
}

.small-card-holder {
  max-width: 250px;
  padding: 0;
  transition: 0.3s;
}

.small-card-holder:hover {
  transform: scale(1.05);
  filter: drop-shadow(0 0 10px rgba(0, 0, 0, 0.3))
}

.small-card-holder img,
.flip-card img {
  width: 100%;
  height: 120px;
  object-fit: cover;
  border-radius: 10px;
}

.flip-card {
  background-color: transparent;
  margin: 0 auto;
  max-width: 250px;
  height: 250px;
  perspective: 1000px; /* Remove this if you don't want the 3D effect */
}

.flip-card-inner {
  border-radius: 10px;
  margin: 0 auto;
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  transition: transform 1s;
  transform-style: preserve-3d;
}

.flip-card-flip .flip-card-inner{
  transform: rotateY(180deg);
}

.flip-card-action:hover .flip-card-inner{
  transform: rotateY(180deg);
  cursor: pointer;
}

/* Position the front and back side */
.flip-card-front, .flip-card-back {
  position: absolute;
  border-radius: 10px;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden; /* Safari */
  backface-visibility: hidden;
}

/* Style the front side (fallback if image is missing) */
.flip-card-front {
  border: 3px solid rgb(235, 235, 235);
  background-color: rgb(255, 255, 255);
  filter: drop-shadow(0 0 10px rgba(0, 0, 0, 0.1));
  color: black;
}

/* Style the back side */
.flip-card-back {
  border: 3px solid #1ad490;
  background-color: #04bf7a;
  color: rgb(255, 255, 255);
  transform: rotateY(180deg);
  filter: drop-shadow(0 0 10px rgba(0, 0, 0, 0.1));
}

.flip-card-back * {
  color: rgb(255, 255, 255)
}

