  display: flex;
  flex-wrap: nowrap;
  flex-direction: column;
  align-items: stretch;
}

#page-holder-400px .grid-item-template {
  max-width: 100%;
}

#page-holder-400px .grids-half-holder{
  flex-direction: column;
}

#page-holder-400px .grid-half-container{
  min-width: 100%;
}

#page-holder-400px .grids-quarter-holder{
  flex-direction: column;
}

#page-holder-400px .grid-quarter-container{
  min-width: 100%;
}